* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
}

.search-form {
  text-align: center;
  background-color: #3563ab;
  margin-bottom: 20px;
  padding: 15px;
}

.search-form input {
  height: 40px;
  padding: 5px;
}

.search-form button {
  height: 40px;
  cursor: pointer;
  background-color: #2196f3;
  border: none;
  border-radius: 8px;
  font-family: inherit;
  display: inline-block;
}

.search-form button:hover {
  background-color: #188ce8;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .12), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16);
}

.gallery {
  flex-wrap: wrap;
  align-items: stretch;
  display: flex;
}

.photo-card {
  border: 2px solid #e5e5e5;
  flex-basis: calc(25% - 11.25px);
}

.photo-card:hover {
  cursor: pointer;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .12), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16);
}

.photo-card:not(:nth-child(4n)) {
  margin-right: 15px;
}

.photo-card:not(:nth-last-child(-n+4)) {
  margin-bottom: 15px;
}

img {
  width: 100%;
  height: 305px;
  margin-bottom: 5px;
  display: block;
}

.info {
  justify-content: space-around;
  padding: 10px;
  display: flex;
}

p {
  text-align: center;
  margin: 0;
}

.info-item:not(:last-child) {
  margin-right: 15px;
}

